              </view>
              <text class="grid-text">ÁîúËúúÈóÆÁ≠î</text>
            </view>
            <view class="grid-item" @click="goToHundredThings">
              <view class="grid-icon-wrapper">
                <view class="grid-icon list-icon">
                  <view class="list-item-small"></view>
