<script>
import { getCoupleInfo, getPartnerInfo, isBound as checkIsBound, clearCoupleInfo } from '../../utils/couple.js';
import { getCoupleStatus, unbindCouple } from '../../api/couple.js';
import { saveCoupleInfo } from '../../utils/couple.js';
import { getUserInfo } from '../../utils/auth.js';

export default {
  data() {
    return {
      statusBarHeight: 0,
      navBarHeight: 54,
      wishText: 'Ëøô‰∏ÄÂàªÁöÑÂπ∏Á¶èË∂≥‰ª•ÊîØÊíë‰ª•ÂêéÁöÑÊº´ÈïøÂ≤ÅÊúà',
      screenWidth: 375,
      // Áî®Êà∑‰ø°ÊÅØ
      userInfo: {
        displayName: '',
        displayAvatar: '',
        avatarUrl: '',
        nickName: ''
      },
      // ÊÉÖ‰æ£ÂÖ≥Á≥ªÁõ∏ÂÖ≥
      isBound: false,
      partnerInfo: null,
      bindTime: '',
      // ËøëÊúüÂä®ÊÄÅ
      recentActivities: []
    };
  },
  computed: {
    // ËÆ°ÁÆóÂú®‰∏ÄËµ∑ÁöÑÂ§©Êï∞
    daysTogether() {
      if (!this.bindTime) return 0;
      try {
        const bindDate = new Date(this.bindTime);
        const now = new Date();
        const diffTime = now - bindDate;
        const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
        return diffDays > 0 ? diffDays : 1; // Ëá≥Â∞ëÊòæÁ§∫1Â§©
      } catch (e) {
        return 0;
      }
    },
    // ËÆ°ÁÆó‰∏ã‰∏Ä‰∏™Âë®Âπ¥Á∫™ÂøµÊó•
    nextAnniversaryDays() {
      if (!this.bindTime) return 0;
      try {
        const bindDate = new Date(this.bindTime);
        const now = new Date();
        const currentYear = now.getFullYear();
        const nextAnniversary = new Date(currentYear, bindDate.getMonth(), bindDate.getDate());
        
        // Â¶ÇÊûú‰ªäÂπ¥ÁöÑÁ∫™ÂøµÊó•Â∑≤ËøáÔºåËÆ°ÁÆóÊòéÂπ¥ÁöÑ
        if (nextAnniversary < now) {
          nextAnniversary.setFullYear(currentYear + 1);
        }
        
        const diffTime = nextAnniversary - now;
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        return diffDays > 0 ? diffDays : 0;
      } catch (e) {
        return 0;
      }
    },
    containerPaddingTop() {
      // Â∞ÜpxËΩ¨Êç¢‰∏∫rpx: rpx = px * 750 / screenWidth
      // Ê∑ªÂä†20rpxÈ¢ùÂ§ñÈó¥Ë∑ù
      const totalHeightPx = this.statusBarHeight + this.navBarHeight;
      const pxToRpx = 750 / this.screenWidth;
      const totalHeightRpx = totalHeightPx * pxToRpx;
      return totalHeightRpx + 20 + 'rpx';
    }
  },
  onLoad() {
    this.getSystemInfo();
    this.loadUserInfo();
    this.loadCoupleInfo();
    this.loadRecentActivities();
  },
  onShow() {
    // ÊØèÊ¨°È°µÈù¢ÊòæÁ§∫Êó∂ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂíåÊÉÖ‰æ£‰ø°ÊÅØ
    this.loadUserInfo();
    this.loadCoupleInfo();
    this.loadRecentActivities();
  },
  methods: {
    getSystemInfo() {

      // ‰ΩøÁî®Êñ∞ÁöÑ API Êõø‰ª£Â∑≤ÂºÉÁî®ÁöÑ getSystemInfoSync
      // #ifdef MP-WEIXIN
      try {
        // Â∞ùËØï‰ΩøÁî®Êñ∞ API
        const windowInfo = wx.getWindowInfo && wx.getWindowInfo();
        const deviceInfo = wx.getDeviceInfo && wx.getDeviceInfo();
        
        if (windowInfo && deviceInfo) {
          this.statusBarHeight = windowInfo.statusBarHeight || 0;
          this.screenWidth = windowInfo.windowWidth || 375;
        } else {
          // ÈôçÁ∫ßÂà∞Êóß API
          const sysInfo = uni.getSystemInfoSync();
          this.statusBarHeight = sysInfo.statusBarHeight || 0;
          this.screenWidth = sysInfo.windowWidth || 375;
        }
      } catch (e) {
        // Â¶ÇÊûúÊñ∞ API ‰∏çÊîØÊåÅÔºåÈôçÁ∫ßÂà∞Êóß API
        const sysInfo = uni.getSystemInfoSync();
        this.statusBarHeight = sysInfo.statusBarHeight || 0;
        this.screenWidth = sysInfo.windowWidth || 375;
      }
      this.navBarHeight = 54;
      // #endif
      // #ifdef H5
      const sysInfoH5 = uni.getSystemInfoSync();
      this.statusBarHeight = sysInfoH5.statusBarHeight || 0;
      this.screenWidth = sysInfoH5.windowWidth || 375;
      this.navBarHeight = 54;
      // #endif
      // #ifndef MP-WEIXIN || H5
      const sysInfoOther = uni.getSystemInfoSync();
      this.statusBarHeight = sysInfoOther.statusBarHeight || 0;
      this.screenWidth = sysInfoOther.windowWidth || 375;
      this.navBarHeight = 54;
      // #endif
    },
    goToSweetQA() {
      // Ë∑≥ËΩ¨Âà∞ÁîúËúúÈóÆÁ≠îÈ°µÈù¢
      uni.navigateTo({
        url: '/pages/qna/index'
      });
    },
    goToHundredThings() {
      // Ë∑≥ËΩ¨Âà∞‰∏ÄÁôæ‰ª∂‰∫ãÈ°µÈù¢
      uni.navigateTo({
        url: '/pages/hundred/index'
      });
    },
    goToHeartWall() {
      // Ë∑≥ËΩ¨Âà∞ÂøÉÂΩ¢Â¢ôÈ°µÈù¢
      uni.navigateTo({
        url: '/pages/heartwall/index'
      });
    },
    goToFutureLetter() {
      // Ë∑≥ËΩ¨Âà∞Êú™Êù•ÊÉÖ‰π¶È°µÈù¢
      uni.navigateTo({
        url: '/pages/xinxiang/index'
      });
    },
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
    loadUserInfo() {
      try {
        const userInfoData = getUserInfo();
        if (userInfoData) {
          this.userInfo = { ...userInfoData };
          
          // Â¶ÇÊûúÊ≤°ÊúâËÆæÁΩÆÊòæÁ§∫ÂêçÁß∞Ôºå‰ΩøÁî®ÂæÆ‰ø°ÊòµÁß∞
          if (!this.userInfo.displayName) {
            this.userInfo.displayName = this.userInfo.nickName || 'Áî®Êà∑';
          }
          
          // Â¶ÇÊûúÊ≤°ÊúâËÆæÁΩÆÊòæÁ§∫Â§¥ÂÉèÔºå‰ΩøÁî®ÂæÆ‰ø°Â§¥ÂÉè
          if (!this.userInfo.displayAvatar) {
            this.userInfo.displayAvatar = this.userInfo.avatarUrl || '/static/zhuye/lanmei_boy.png';
          }
        } else {
          // ‰ªéÁôªÂΩï‰ø°ÊÅØ‰∏≠Ëé∑Âèñ
          const loginInfo = uni.getStorageSync('login_info');
          if (loginInfo && loginInfo.userInfo) {
            this.userInfo = { ...loginInfo.userInfo };
          }
        }
      } catch (error) {
        console.error('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•', error);
      }
    },
    // Âä†ËΩΩÊÉÖ‰æ£‰ø°ÊÅØ
    async loadCoupleInfo() {
      try {
        // ÂÖàÊ£ÄÊü•Êú¨Âú∞
        const localCoupleInfo = getCoupleInfo();
        if (localCoupleInfo && localCoupleInfo.isBound) {
          this.isBound = true;
          this.partnerInfo = localCoupleInfo.partnerInfo || null;
          this.bindTime = localCoupleInfo.bindTime || '';
          
          // ÂêåÊó∂‰ªéÊúçÂä°Âô®ÂêåÊ≠•‰∏ÄÊ¨°Áä∂ÊÄÅ
          try {
            const response = await getCoupleStatus();
            if (response && response.data) {
              if (response.data.isBound) {
                // Êõ¥Êñ∞Êú¨Âú∞‰ø°ÊÅØ
                saveCoupleInfo({
                  isBound: true,
                  coupleId: response.data.coupleId,
                  partnerId: response.data.partnerInfo?.userId || '',
                  partnerInfo: response.data.partnerInfo || {},
                  bindTime: response.data.bindTime || '',
                  role: response.data.role || ''
                });
                this.partnerInfo = response.data.partnerInfo || {};
                this.bindTime = response.data.bindTime || '';
              } else {
                // ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÊ∏ÖÈô§Êú¨Âú∞Áä∂ÊÄÅ
                console.log('‚ö†Ô∏è ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÊ∏ÖÈô§Êú¨Âú∞Áä∂ÊÄÅ');
                clearCoupleInfo();
                this.isBound = false;
                this.partnerInfo = null;
                this.bindTime = '';
              }
            }
          } catch (e) {
            console.error('ÂêåÊ≠•ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
            // ÂêåÊ≠•Â§±Ë¥•Êó∂‰øùÊåÅÊú¨Âú∞Áä∂ÊÄÅ
          }
          return;
        }
        
        // Êú¨Âú∞Ê≤°ÊúâÔºåÊü•ËØ¢ÊúçÂä°Âô®
        this.isBound = false;
        this.partnerInfo = null;
        try {
          const response = await getCoupleStatus();
          if (response && response.data) {
            if (response.data.isBound) {
              this.isBound = true;
              this.partnerInfo = response.data.partnerInfo || {};
              this.bindTime = response.data.bindTime || '';
              
              // ‰øùÂ≠òÂà∞Êú¨Âú∞
              saveCoupleInfo({
                isBound: true,
                coupleId: response.data.coupleId,
                partnerId: response.data.partnerInfo?.userId || '',
                partnerInfo: response.data.partnerInfo || {},
                bindTime: response.data.bindTime || '',
                role: response.data.role || ''
              });
            } else {
              // ÊúçÂä°Âô®ËøîÂõûÊú™ÁªëÂÆöÔºåÁ°Æ‰øùÊú¨Âú∞‰πüÊòØÊú™ÁªëÂÆöÁä∂ÊÄÅ
              this.isBound = false;
              this.partnerInfo = null;
              this.bindTime = '';
              clearCoupleInfo();
            }
          }
        } catch (e) {
          console.error('Êü•ËØ¢ÁªëÂÆöÁä∂ÊÄÅÂ§±Ë¥•', e);
          // Êü•ËØ¢Â§±Ë¥•Êó∂‰ΩøÁî®Êú¨Âú∞Áä∂ÊÄÅ
          this.isBound = checkIsBound();
          if (this.isBound) {
            this.partnerInfo = getPartnerInfo();
            const coupleInfo = getCoupleInfo();
            this.bindTime = coupleInfo ? coupleInfo.bindTime : '';
          }
        }
      } catch (e) {
        console.error('Âä†ËΩΩÊÉÖ‰æ£‰ø°ÊÅØÂ§±Ë¥•', e);
        this.isBound = checkIsBound();
        if (this.isBound) {
          this.partnerInfo = getPartnerInfo();
        }
      }
    },
    // Âä†ËΩΩËøëÊúüÂä®ÊÄÅ
    loadRecentActivities() {
      // Ê®°ÊãüÂä®ÊÄÅÊï∞ÊçÆÔºåÂÆûÈôÖÂ∫îËØ•‰ªéAPIËé∑Âèñ
      this.recentActivities = [
        {
          icon: 'üì∏',
          text: 'ÂÖ±ÂêåÂàõÂª∫‰∫Ü"2024ÊóÖË°åËÆ∞ÂøÜ"Áõ∏ÂÜå'
        },
        {
          icon: 'üèÜ',
          text: 'ËææÊàêÊàêÂ∞±"ÁæéÈ£üÊé¢Èô©ÂÆ∂"'
        },
        {
          icon: 'üìÖ',
          text: '2025Âπ¥11Êúà10Êó• Á∫™ÂøµÊó•Âç≥Â∞ÜÂà∞Êù•'
        }
      ];
    },
    // Ë∑≥ËΩ¨Âà∞ÈÇÄËØ∑È°µÈù¢
    goToInvite() {
      uni.navigateTo({
        url: '/pages/invite/index'
      });
    },
    // Ë∑≥ËΩ¨Âà∞‰∏™‰∫∫‰∏≠ÂøÉ
    goToProfile() {
      uni.navigateTo({
        url: '/pages/we/index'
      });
    },
    // Ëß£ÁªëÂÖ≥Á≥ª
    async handleUnbind() {
      uni.showModal({
        title: 'Á°ÆËÆ§Ëß£Áªë',
        content: 'Ëß£Èô§ÂÖ≥Á≥ªÂêéÔºåÂèåÊñπÂ∞ÜÊó†Ê≥ïÂÖ±‰∫´Êï∞ÊçÆ„ÄÇÁ°ÆÂÆöË¶ÅËß£Èô§ÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'Ëß£Áªë‰∏≠...' });
              await unbindCouple();
              uni.hideLoading();
              
              // Ê∏ÖÈô§Êú¨Âú∞‰ø°ÊÅØ
              clearCoupleInfo();
              
              uni.showToast({ 
                title: 'Â∑≤Ëß£Èô§ÂÖ≥Á≥ª', 
                icon: 'success' 
              });
              
              // Êõ¥Êñ∞È°µÈù¢Áä∂ÊÄÅ
              this.isBound = false;
              this.partnerInfo = null;
              this.bindTime = '';
              
              // Âª∂ËøüÂà∑Êñ∞È°µÈù¢
              setTimeout(() => {
                this.loadCoupleInfo();
              }, 1500);
            } catch (error) {
              uni.hideLoading();
              console.error('Ëß£ÁªëÂ§±Ë¥•', error);
              uni.showToast({ 
                title: error.message || 'Ëß£ÁªëÂ§±Ë¥•ÔºåËØ∑ÈáçËØï', 
                icon: 'none' 
              });
            }
          }
        }
      });
    }
  }
};
</script>

